@import url(https://fonts.cdnfonts.com/css/neue-haas-grotesk-display-pro);
@import url(https://fonts.cdnfonts.com/css/neue-haas-grotesk-display-pro);
@import url(https://fonts.cdnfonts.com/css/clash-display);
body {
  margin: 0;
  font-family: "Neue Haas Grotesk Display Pro", -apple-system,
    BlinkMacSystemFont, "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* border: 1px solid #555; */
}

body {
  width: 100vw;
  background: #000;
}

.App {
  width: 100%;
  min-height: 100vh;
  position: relative;
  text-align: center;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

/* Tunnel */
.portalWrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  opacity: 1;
  z-index: -1;
}

.portal {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Media Queries */

/* Devices with 768px width and below. (Phones and Tablets) */
@media only screen and (max-width: 768px) {
  .portalWrapper {
    opacity: 0.75;
  }
}

@media only screen and (max-width: 468px) {
}

/* Portfolio Container */
#portfolio {
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  overflow: hidden;
}

#portfolioContainer {
  width: 100%;
  margin-left: 2em;
  margin-right: 2em;
}

#slogan {
  margin-bottom: 15em;
}

.sloganDesc {
  text-align: start;
  color: #ddd;
  font-size: 1.2em;
  letter-spacing: -0.3px;
  font-weight: 600;
}

.subheading {
  text-align: start;
  color: #fff;
  opacity: 0.6;
  font-size: 0.5em;
  font-weight: 500;
  text-transform: uppercase;
  height: 2em;
}

/* Portfolio Elements */
.portfolioMenu {
  width: 100%;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

.portfolioLinkWrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 0.25em;
}

.portfolioLink {
  position: relative;
  width: 30%;

  display: flex;
  align-items: baseline;
  justify-content: space-between;

  font-size: 0.9em;
  line-height: 1.5em;
  text-transform: uppercase;
  background: -webkit-linear-gradient(left, #fff 60%, #555);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#playground {
  margin-top: 1.75em;
  margin-bottom: 3em;
}

/* Portfolio Description */
.portfolioDesc {
  display: none;
  width: 30%;
}

.portfolioLink:hover,
.portfolioLink:focus,
.portfolioLink:active {
  margin-top: 0.75em;
  background: -webkit-linear-gradient(left, #2c73fe, #89cff0);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.portfolioLink:hover + .portfolioDesc,
.portfolioLink:focus + .portfolioDesc,
.portfolioLink:active + .portfolioDesc {
  display: block;
}

#desc {
  color: #fff;
  font-weight: 400;
  text-align: start;
  font-size: 0.9em;
  letter-spacing: 1px;
}

/* Devices with 768px width and below. (Phones and Tablets) */
@media only screen and (max-width: 768px) {
  #portfolioContainer {
    margin-left: 1.2em;
    margin-right: 1.2em;
  }

  #slogan {
    margin-bottom: 50vw;
  }

  .sloganDesc {
    font-size: 1em;
  }

  .portfolioLinkWrapper {
    display: block;
  }

  .portfolioLink {
    width: 100%;
    font-size: 0.8em;
  }

  .portfolioDesc {
    width: 100%;
    margin-top: 0.5em;
    margin-bottom: 1em;
  }

  #playground {
    margin-top: 1.5em;
    margin-bottom: 2em;
  }

  #desc {
    font-size: 0.75em;
  }
}

/* Footer */
.copyrightWrapper {
  position: absolute;
  bottom: 2%;
  right: 1.5em;
}

#copyright {
  color: #555;
  font-size: 0.8em;
}

#streak {
  position: absolute;
  bottom: 0%;
  background-color: #2c73fe;
  height: 2px;
  width: 100vw;
}

/* Devices with 768px width and below. (Phones and Tablets) */
@media only screen and (max-width: 768px) {
  .copyrightWrapper {
    display: none;
  }
}

/* Header */
#header {
  position: fixed;
  width: 100%;
  color: #fff;
  margin-top: 1.75em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
}

.headerNameWrapper {
  height: 100%;
  margin-left: 2em;
  display: flex;
  align-items: flex-end;
}

#headerName {
  font-size: 1.5em;
  text-transform: uppercase;
  letter-spacing: -0.02em;
}

#headerPronoun {
  color: #555;
  margin-left: 1em;
  margin-bottom: 0.25em;
  font-size: 0.8em;
  font-weight: 200;
  letter-spacing: -0.02em;
}

#headerInfoLink {
  height: 100%;
  margin-right: 2em;
  color: #fff;
  text-decoration: none;
}

.headerInfo {
  display: flex;
  align-items: center;
  text-align: center;
  font-size: 0.8em;
}

#headerInfo {
  text-transform: uppercase;
  letter-spacing: -0.02em;
  margin-right: 0.5em;
}

.hidden {
  color: #222 !important;
  background: #222 !important;
}

/* Devices with 768px width and below. (Phones and Tablets) */
@media only screen and (max-width: 768px) {
  #header {
    margin-top: 1.25em;
  }

  .headerNameWrapper {
    margin-left: 1.25em;
  }

  #headerName {
    font-size: 1em;
    letter-spacing: -0.03em;
  }

  #headerPronoun {
    margin-left: 1em;
    font-size: 0.6em;
    font-weight: 200;
  }

  #headerInfoLink {
    margin-right: 1.25em;
  }

  .headerInfo {
    font-size: 0.7em;
  }
}

/* Info Page */
#info {
  width: 100%;
  padding-left: 2em;
  padding-right: 2em;
}

#aboutMe {
  padding-top: 15em;
  text-align: start;
}

/* Biography */
.infoOccupation {
  width: 50%;
  font-size: 1.5em;
  font-weight: 600;
}

.infoShape,
.infoPassion {
  width: 50%;
  margin-top: 1.25em;

  font-size: 1.2em;
  font-weight: 400;
  opacity: 0.75;
}

/* Approach */
#approach {
  padding-top: 10em;
  text-align: start;
}

.infoProvide {
  font-size: 1.2em;
  font-weight: 500;
}

#infoProcess {
  margin-top: 2em;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.processContainer {
  width: 100%;
}

/* Process Description */
.processWrapper {
  display: flex;
  align-items: center;
}

.processNum {
  font-family: "Clash Display", sans-serif;
  color: #555;
}

.processDesc {
  width: 75%;
  color: #fff;
  opacity: 0.75;
  font-size: 0.9em;
  font-weight: 500;
}

.layers {
  width: 25%;
}

/* Borders */
#leftBorder {
  width: 1em;
  height: 5em;
  border: 0.5px solid #555;
  position: relative;
  margin-right: 1em;
}

#leftBorder:after {
  content: "";
  display: block;
  position: absolute;
  width: 1em;
  background-color: #040508;
  left: 5px;
  top: -1px;
  bottom: -1px;
  z-index: 1;
}

#rightBorder {
  width: 1em;
  height: 5em;
  border: 0.5px solid #555;
  position: relative;
  margin-left: 1em;
}

#rightBorder:after {
  content: "";
  display: block;
  position: absolute;
  width: 1em;
  background-color: #040508;
  right: 5px;
  top: -1px;
  bottom: -1px;
  z-index: 1;
}

/* Contact */
#contact {
  padding-top: 10em;
  padding-bottom: 5em;
  text-align: start;
}

.contactMe {
  font-size: 3em;
  color: #fff;
}

.contactContainer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#contactDesc {
  width: 60%;
}

#speechBubble {
  width: 40%;
  height: 100%;
}

.contactMailWrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contactMail {
  position: absolute;
  color: #fff;
  font-size: 1.25em;
  background-color: transparent;
  border: none;
}

#mail {
  line-height: 30px;
  border-bottom: 1px solid #fff;
}

.contactMail:hover {
  cursor: pointer;
}

/* Descriptions */
.infoDesc {
  color: #fff;
}

/* Subheaders */
.subheading {
  text-align: start;
  color: #fff;
  opacity: 0.6;
  font-size: 0.7em;
  font-weight: 500;
  text-transform: uppercase;
  height: 2em;
}

